/* eslint no-eval: 0 */

import React, { Component } from 'react'
import './Calculator.css'

//components
import Button from '../components/Button'
import Display from '../components/Display'

//footer
import Footer from '../footer/Footer'

//initial state
const initialState = {

    displayValue: '0',
    clearDisplay: false,
    operation: null,
    values: [0,0],
    current: 0

}

export default class Calculator extends Component{

    constructor(props){
        super(props)
        this.clearMemory = this.clearMemory.bind(this)
        this.setOperation = this.setOperation.bind(this)
        this.addDigit = this.addDigit.bind(this)
    }

    state = {
        ...initialState
    }

    //calculator functions
    clearMemory(){
        this.setState({...initialState})
    }

    setOperation(op){
        if(this.state.current === 0){
            this.setState({
                operation: op,
                current: 1,
                clearDisplay: true
            })
        } else {
            const result = op === '='
            const currentOp = this.state.operation

            const values = [...this.state.values]
                try { 
                    values[0] = eval(`${values[0]} ${currentOp} ${values[1]}`)
                } catch(e) {
                    values[0] = this.state.values[0]
                }

            values[1] = 0

            this.setState({
                displayValue: values[0],
                operation: result ? null : op,
                current: result ? 0 : 1,
                clearDisplay: !result,
                values
            })
        }
    }

    addDigit(n){
        if(n === '.' && this.state.displayValue.includes('.')){
            return
        }
        const clearDisplay = this.state.displayValue === '0'
        || this.state.clearDisplay

        const currentValue = clearDisplay ? '' : this.state.displayValue

        if(currentValue.length > 10){
            return
        }

        const displayValue = currentValue + n
        this.setState({displayValue, clearDisplay: false})

        if(n !== '.'){
            const i = this.state.current
            const newValue = parseFloat(displayValue)

            const values = [...this.state.values]
            values[i] = newValue
            this.setState({ values })

            console.log(values)
        }

    }
    //end of calculator functions

    //start of render
    render(){
        return(
            <div className="calculator-body">
                <h1>Calculator</h1>
                <div className="calculator">
                    <Display value={this.state.displayValue}></Display>
                    <Button label="AC" triple click={this.clearMemory}></Button>
                    <Button label="/" operation click={this.setOperation}></Button>
                    <Button label="7" click={this.addDigit}></Button>
                    <Button label="8" click={this.addDigit}></Button>
                    <Button label="9" click={this.addDigit}></Button>
                    <Button label="*" operation click={this.setOperation}></Button>
                    <Button label="4" click={this.addDigit}></Button>
                    <Button label="5" click={this.addDigit}></Button>
                    <Button label="6" click={this.addDigit}></Button>
                    <Button label="-" operation click={this.setOperation}></Button>
                    <Button label="1" click={this.addDigit}></Button>
                    <Button label="2" click={this.addDigit}></Button>
                    <Button label="3" click={this.addDigit}></Button>
                    <Button label="+" operation click={this.setOperation}></Button>
                    <Button label="0" double click={this.addDigit}></Button>
                    <Button label="." click={this.addDigit}></Button>
                    <Button label="=" operation click={this.setOperation}></Button>
                </div>
                <Footer></Footer>
            </div>
        )
    }

}