import React from 'react'
import { library } from '@fortawesome/fontawesome-svg-core'
import { faDivide, faMinus, faEquals, faPlus, faTimes } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

//stylesheet
import './Button.css'

//font-awesome library
library.add( faDivide, faMinus, faEquals, faTimes, faPlus)

export default props => {

    function setTitle(label){
        switch(label){
            case '/':
                return <FontAwesomeIcon icon="divide" />
            case '+':
                return <FontAwesomeIcon icon="plus" />
            case '-':
                return <FontAwesomeIcon icon="minus" />
            case '*':
                return <FontAwesomeIcon icon="times" />
            case '=':
                return <FontAwesomeIcon icon="equals" />
            default:
                return props.label
        }
    }

    //start of render
    return(
        <button className={
            `button 
            ${props.operation ? 'operation' : ''}
            ${props.double ? 'double' : ''}
            ${props.triple ? 'triple' : ''}
            `
        }
        onClick={e => props.click && props.click(props.label)}
        >{setTitle(props.label)}</button>
    )

}